import React from 'react'

import ImageGallery from 'react-image-gallery';

import '../slideshow.scss';
 
const images = [
    {
        original: './assets/balkon3.jpg',
        thumbnail: './assets/balkon3.jpg',
    },
    {
        original: './assets/soba3.jpg',
        thumbnail: './assets/soba3.jpg',
    },
    {
        original: './assets/balkon2.jpg',
        thumbnail: './assets/balkon2.jpg',
    },
    {
        original: './assets/vece.jpg',
        thumbnail: './assets/vece.jpg',
    },
    {
        original: './assets/soba3.jpg',
        thumbnail: './assets/soba3.jpg',
    },
    {
        original: './assets/balkon2.jpg',
        thumbnail: './assets/balkon2.jpg',
    },
];
 
class MyGallery2 extends React.Component {
  render() {
    return (
        <div className='slide'>
            <ImageGallery items={images} />
        </div>
    );
  }
}

export default MyGallery2;