class Popup3 extends Component{
    render() {
        return(
            <div className="popup" id="popup3">
                <div className="popup__content">
                    <a href="#apartments" class="popup__close">&times;</a>
                    <MyGallery3 />
                </div>
            </div>
        )
    }
};

export default Popup3;